<template>
  <div class="gold">
    <img class="market_bg" :src="require('@/assets/market.png')" alt="">
    <div class="change">
      <div class="changeBox" @click="changeIdentity">
        <div class="changeIcon">
          <img src="@/assets/change.png" alt="">
        </div>
        <div class="nowName">您当前身份为{{ CurrentIdentity === 'serve' ? '人才' : '雇主' }}</div>
      </div>
      <div class="myinfo">
        <div class="service">
          <img :src="require(`@/assets/${CurrentIdentity === 'serve' ? 'service' : 'demand'}.png`)" alt="">
        </div>
        <div class="myService" @click="inMyNeeds">我的{{ CurrentIdentity === 'serve' ? '服务' : '需求' }}</div>
      </div>
    </div>
    <div class="main" v-if="CurrentIdentity === 'employer'">
      <div class="form">
        <div class="title">快速发布需求</div>
        <van-field class="input" v-model="value" label="我需要" placeholder="如：智能合约设计" />
      </div>
      <van-button type="info" block @click="releaseDemand('')" color="#1F75D9">确认发布</van-button>
      <div class="tips">已有1000家企业选择STAR SHOW发布区块链项目</div>
    </div>
    <div class="demandWall" v-if="user_id">
      <van-swipe :loop="false" :width="swiperList.length == 1 ? 'auto' : '300'" :show-indicators="false">
        <template v-if="CurrentIdentity === 'serve'">
          <van-swipe-item v-for="(item, index) in swiperList" :key="index"
            :class="CurrentIdentity !== 'serve' ? 'edit' : ''">
            <div class="sItem">
              <div class="select">{{ $util.CurrentUserStatus(item.task.my_bid_status) }}</div>
              <div class="sTitle">
                <div>{{ item.task.title }}</div>
                <!-- <span v-if="CurrentIdentity !== 'serve'" @click="edit()">编辑</span> -->
              </div>
              <div class="sTips">
                <div class="v1">{{ $util.taskType(item.task.type) }}</div>
                <div class="v2">{{ item.task.amount }} {{ item.task.bail_type === 1 ? 'SRS' : 'USDT' }}</div>
              </div>
              <div class="status">
                <div class="state">{{ $util.taskStatusTips(CurrentIdentity, item.task.my_status, item.task.status) }}
                </div>
                <div class="tips">{{ $util.bidStatus(item.log.type) }}</div>
              </div>
            </div>
          </van-swipe-item>
        </template>
        <template v-else>
          <van-swipe-item v-for="(item, index) in swiperList" :key="index"
            :class="CurrentIdentity !== 'serve' ? 'edit' : ''">
            <div class="sItem">
              <!-- <div class="select">{{ $util.CurrentUserStatus(item.my_bid_status) }}</div> -->
              <div class="sTitle">
                <div>{{ item.title }}</div>
                <span v-if="CurrentIdentity !== 'serve'" @click="edit(item.id)">编辑</span>
              </div>
              <div class="sTips">
                <div class="v1">{{ $util.taskType(item.type) }}</div>
                <div class="v2">{{ item.amount }} {{ item.bail_type === 1 ? 'SRS' : 'USDT' }}</div>
              </div>
              <div class="status">
                <div class="state">{{ $util.taskStatusTips(CurrentIdentity, item.my_status, item.status) }}
                </div>
                <div class="tips">{{ $util.bidStatus(item.log.type) }}</div>
              </div>
            </div>
          </van-swipe-item>
        </template>
      </van-swipe>
    </div>
    <van-tabs @change="tabChange" v-model="activeName">
      <van-tab :title="el.name" v-for="(el, i) in tab" :key="i" :name="i">
        <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad(i)"
          :immediate-check="false">
          <div class="card" v-for="(item, index) in el.list" :key="index">
            <div class="cardMain">
              <div class="cardMainL">
                <div class="userIcon">
                  <img :src="require('@/assets/user.png')" alt="">
                </div>
                <!-- <div class="follow" @click="release" :style="{ color: item.followStatus ? '#909CBA' : '#1F75D9' }">{{
                    item.followStatus ?
                      '已关注' : '+关注'
                }}</div>
                <div class="followNum">{{ item.followNum }}人已关注</div> -->
              </div>
              <div class="cardMainR" @click="release(item.id)">
                <div class="orderName">{{ item.title }}</div>
                <!-- <div class="orderNum" v-if="item.orderNum">服务订单量 {{ item.orderNum }}</div> -->
                <div class="description">{{ item.description }}</div>
                <div class="price">
                  <div class="commission">佣金</div>
                  <template v-if="item.amount !== -1">
                    <div class="nums">{{ item.amount }}</div>
                    <!-- <div class="auxiliary">起</div> -->
                    <div class="unit">USDT</div>
                  </template>
                  <template v-else>
                    <div class="nums">面议</div>
                  </template>
                </div>
                <!-- <div class="userTage">
                  <div class="tage" v-for="(tag, k) in item.tage" :key="k">{{ tag }}</div>
                </div> -->
                <!-- <div class="other">
                  <div class="praise">好评率 {{ item.favorableRate }}%</div>
                  <div class="praise">{{ item.favorableTage }}</div>
                </div> -->
              </div>
            </div>
            <van-divider class="line" />
            <div class="cardbtn">
              <div class="releaseTime">发布时间：{{ $util.getUnixToDate('yyyy-mm-dd hh:mm', item.create_ts)
              }}</div>
              <!-- <div class="onLine" v-else>
                <div class="onLineStatus">
                  <img v-if="item.onLine" :src="require('@/assets/online.png')" alt="">
                  <img v-else :src="require('@/assets/noline.png')" alt="" />
                </div>
                <div class="onLineText" :style="{ color: item.onLine ? '#1F75D9' : '#909CBA' }">{{ item.onLine ? '在线' :
                    '2小时前在线'
                }}</div>
              </div> -->
              <div class="serviceBtn" v-if="CurrentIdentity === 'serve' && item.isShow">
                <div class="end">
                  <van-count-down :time="item.countDownTime" format="DD天HH小时" />后截止报名
                </div>
                <div class="btns" v-if="user_id != item.user_id" @click="release(item.id)">抢需求</div>
              </div>
            </div>
          </div>
        </van-list>
      </van-tab>
    </van-tabs>
    <!-- <div class="isLogin" v-if="isLogin">
      <div class="loginTips">登录星秀，发现精彩</div>
      <div class="reClose">
        <van-button class="login" type="default" round @click="login">登录</van-button>
        <div class="close" @click="isLogin = false">
          <img src="@/assets/LoginBtnClose.png" alt="">
        </div>
      </div>
    </div> -->
  </div>
</template>
<script>
import { Toast, Dialog } from 'vant';
import { checkUserLogin } from '@/util/bridge'
import { taskList, taskOwner, bidderOwner } from '@/api/request'

export default {
  data () {
    return {
      isLogin: false,
      user_id: localStorage.getItem('userId'),
      index: 0,
      activeName: 0,
      CurrentIdentity: '',
      tab: [
        {
          name: '代码审计',
          list: []
        },
        {
          name: '讲师招募',
          list: []
        },
        {
          name: '智能合约',
          list: []
        }
      ],
      list: [],
      loading: false,
      finished: false,
      value: '',
      cursor: '',
      DateNow: new Date(new Date().toLocaleDateString()).getTime(),
      swiperList: []
    }
  },
  created () {
    let loca = localStorage.getItem('CurrentIdentity')
    if (loca) {
      this.$store.dispatch('ActionsSetUserType', loca)
    } else {
      loca = this.$store.state.CurrentIdentity
    }
    this.CurrentIdentity = loca
  },
  mounted () {
    console.log('mounted', this.CurrentIdentity)
    this.getSwipeData()
    this.onLoad(0)
    if (!this.user_id) this.isLogin = true;
  },
  methods: {
    env () {
      return process.env.NODE_ENV === 'development' ? true : false
    },
    async login () {
      await checkUserLogin()
    },
    async getSwipeData () {
      if (!this.user_id) return
      let fn = this.CurrentIdentity === 'serve' ? bidderOwner : taskOwner
      let res = await fn({
        status: 0,
        cursor: '',
        user_id: this.user_id
      })
      this.swiperList = res.data.splice(0, 3)
    },
    changeIdentity () {
      this.$router.push('chooseIdentity')
    },
    edit (id) {
      this.releaseDemand(id)
    },
    async release (id) {
      if (this.env()) {
        this.$router.push({
          path: 'demandDetails',
          query: {
            id: id
          }
        })
      } else {
        let res = await checkUserLogin()
        if (res.token && res.userId !== -1) {
          this.$router.push({
            path: 'demandDetails',
            query: {
              id: id
            }
          })
          localStorage.setItem('token', res.token)
          localStorage.setItem('userId', res.userId)
        }
      }
    },
    async releaseDemand (id) {
      if (this.env()) {
        this.$router.push({
          path: 'information',
          query: {
            desc: this.value,
            id: id
          }
        })
      } else {
        let res = await checkUserLogin()
        if (res.token && res.userId !== -1) {
          this.$router.push({
            path: 'information',
            query: {
              desc: this.value,
              id: id
            }
          })
          localStorage.setItem('token', res.token)
          localStorage.setItem('userId', res.userId)
        }
      }
    },
    async onLoad (index) {
      console.log(index, this.activeName)
      let res = await taskList({
        type: this.activeName + 1,
        cursor: this.cursor
      })
      if (this.cursor === '') {
        this.tab[index].list = []
      }
      this.loading = false;
      if (res.data.length === 0) {
        this.finished = true;
      } else {
        this.cursor = res.data[res.data.length - 1].create_ts
        res.data.forEach(el => {
          if ((this.DateNow) === el.begin_time) {
            el.countDownTime = (this.DateNow + 86400000) - Date.now()
          } else {
            el.countDownTime = el.begin_time - Date.now()
          }
          if (this.DateNow > el.begin_time) {
            el.isShow = false
          } else {
            el.isShow = true
          }
        })
      }
      this.tab[index].list = this.tab[index].list.concat(res.data)
      console.log(this.tab[index].list)
    },
    async inMyNeeds () {
      if (this.env()) {
        if (this.CurrentIdentity === 'serve') {
          // 服务
          this.$router.push('myService')
        } else {
          // 需求
          this.$router.push('myNeeds')
        }
      } else {
        let res = await checkUserLogin()
        if (res.token && res.userId !== -1) {
          if (this.CurrentIdentity === 'serve') {
            // 服务
            this.$router.push('myService')
          } else {
            // 需求
            this.$router.push('myNeeds')
          }
          localStorage.setItem('token', res.token)
          localStorage.setItem('userId', res.userId)
        }
      }
    },
    tabChange (e) {
      this.finished = false
      this.loading = true
      this.cursor = ''
      this.onLoad(e)
    }
  }
}
</script>
